@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  background-color: #a6dcf3;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  --main-color: 32, 32, 32;
  --shadow-color: 150, 150, 150;
  --green: 97, 223, 101;
  --red: 253, 95, 95;
  --addition-hue: 0;
  --subtraction-hue: 120;
  --multiplication-hue: 197;
  --division-hue: 60;
  --mode-normal: 100%, 80%;
  --mode-light: 50%, 87%;
  --xsmall: 14px;
  --small: 22px;
  --medium: 35px;
  --medium-large: 40px;
  --large: 60px;
  --xlarge: 100px;
  --xxlarge: 130px;
  color: rbg(var(--main-color));
  font-family: 'Fredoka One';
  height: calc(100vh - 30px);
  padding: 15px;
  width: calc(100vw - 30px);
}

@media only screen and (min-width: 768px) {
  .App {
    height: calc(100vh - 60px);
    padding: 30px;
    width: calc(100vw - 60px);
  }
}

.link-button, button {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden; /* hover scale needs to do less work */
  border: 1px solid rgb(var(--main-color));
  border-radius: 0.6em;
  box-shadow: 0px 0px 5px rgb(var(--shadow-color));
  box-sizing: border-box;
  display: inline-block;
  font-family: 'Fredoka One';
  margin: 0.3em;
  outline: none;
  padding: 0.3em 0.3em 0.3em 0;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}
.link-button, .link-button:link, .link-button:visited, .link-button:hover, .link-button:focus, .link-button:active{
  color: rgb(var(--main-color));
  text-decoration: none;
}
.link-button:hover, button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.link-button > *, .button > * {
  margin-left: 0.3em;
}

.link-button--xsmall {
  font-size: var(--xsmall);
}

.link-button--small {
  font-size: var(--small);
}

.link-button--medium {
  font-size: var(--medium);
}

.link-button--large {
  font-size: var(--large);
}

.link-button--addition {
  background-color: hsl(var(--addition-hue), var(--mode-normal));
}
.link-button--addition:active {
  background-color: white;
}
.link-button--subtraction {
  background-color: hsl(var(--subtraction-hue), var(--mode-normal));
}
.link-button--subtraction:active {
  background-color: white;
}
.link-button--multiplication {
  background-color: hsl(var(--multiplication-hue), var(--mode-normal));
}
.link-button--multiplication:active {
  background-color: white;
}
.link-button--division {
  background-color: hsl(var(--division-hue), var(--mode-normal));
}
.link-button--division:active {
  background-color: white;
}
.link-button--app {
  background-color: white;
}
.link-button--app:active {
  background-color: #cccccc;
}

input[type="text"], input[type="number"] {
  background-color: white;
  border: 1px solid #cdcdcd;
  border-radius: 20px;
  box-shadow: 0px 0px 5px rgb(var(--shadow-color));
  font-family: 'Fredoka One';
  outline: none;
}
::-webkit-input-placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}
::-moz-placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}
:-ms-input-placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}
::-ms-input-placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}
::placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}

.background--addition {
  background-color: hsl(var(--addition-hue), var(--mode-normal));
}
.background--subtraction {
  background-color: hsl(var(--subtraction-hue), var(--mode-normal));
}
.background--multiplication {
  background-color: hsl(var(--multiplication-hue), var(--mode-normal));
}
.background--division {
  background-color: hsl(var(--division-hue), var(--mode-normal));
}

.background-light--addition {
  background-color: hsl(var(--addition-hue), var(--mode-light));
}
.background-light--subtraction {
  background-color: hsl(var(--subtraction-hue), var(--mode-light));
}
.background-light--multiplication {
  background-color: hsl(var(--multiplication-hue), var(--mode-light));
}
.background-light--division {
  background-color: hsl(var(--division-hue), var(--mode-light));
}

.user-list {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.user-list__user {
  font-size: var(--small);
  margin-top: 15px;
  padding: 8px 15px;
  text-align: center;
}

.user-list__form {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 15px;
}

.user-list__create {
  background-color: rgb(var(--green));
  color: white;
  font-size: var(--small);
  padding: 8px 15px;
}

.user-list__username {
  box-sizing: border-box;
  font-size: var(--small);
  margin: 8px;
  padding: 8px 15px;
  width: 260px;
}

@media only screen and (min-width: 500px) {

  .user-list__form {
    flex-direction: row;
  }

}

@media only screen and (min-width: 768px) {

  .user-list__user {
    font-size: var(--medium);
    margin-top: 20px;
    padding: 10px 20px;
  }

  .user-list__create {
    font-size: var(--medium);
    padding: 10px 20px;
  }

  .user-list__username {
    font-size: var(--medium);
    margin: 10px;
    padding: 10px 20px;
    width: 400px;
  }

}
.game {
  align-items: center;
  border: 3px solid hsl(0, 0%, 100%);
  border-radius: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 40px 20px 30px 20px;
  position: relative;
  width: 280px;
}

.game__home {
  font-size: var(--small);
  left: 0;
  position: absolute;
  top: 0;
}

.game__question {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-size: var(--medium-large);
  justify-content: center;
}

.game__answer-wrapper {
  margin-top: 10px;
  position: relative;
}

.game__answer {
  font-size: var(--medium-large);
  padding: 5px 10px;
  text-align: center;
  width: 100px;
}

@-webkit-keyframes game__animation {
  0% { opacity: 1; }
  100% { opacity: 0 }
}

@keyframes game__animation {
  0% { opacity: 1; }
  100% { opacity: 0 }
}
.game__answer-correct {
  -webkit-animation: 1s ease-out game__animation;
          animation: 1s ease-out game__animation;
  border-radius: 20px;
  box-shadow: 0px 0px 10px 5px rgb(var(--green));
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.game__answer-incorrect {
  -webkit-animation: 1s ease-out game__animation;
          animation: 1s ease-out game__animation;
  border-radius: 20px;
  box-shadow: 0px 0px 10px 5px rgb(var(--red));
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.game__status {
  background-color: white;
  border: 1px solid rgb(var(--main-color));
  border-radius: 20px;
  height: 25px;
  margin-top: 30px;
  overflow: hidden;
  width: 100%;
}

.game__progress {
  background-color: rgb(var(--green));
  height: 25px;
  -webkit-transition: width 0.2s ease-out;
  transition: width 0.2s ease-out;
}

@media only screen and (min-width: 500px) {
  .game {
    padding: 40px 20px 40px 20px;
    width: 450px;
  }

  .game__question {
    font-size: var(--large);
  }

  .game__answer {
    font-size: var(--large);
    padding: 6px 12px;
    width: 130px;
  }

  .game__status {
    margin-top: 50px;
  }

}

@media only screen and (min-width: 800px) {
  .game {
    width: 740px;
    padding: 60px 20px 50px 20px;
  }

  .game__question {
    flex-direction: row;
    font-size: var(--xlarge);
  }

  .game__answer-wrapper {
    margin-left: 30px;
  }

  .game__answer {
    font-size: var(--xlarge);
    padding: 7px 15px;
    text-align: left;
    text-align: initial;
    width: 200px;
  }

  .game__status {
    height: 50px;
    margin-top: 60px;
    width: 550px;
  }

  .game__progress {
    height: 50px;
  }
}

@media only screen and (min-width: 1100px) and (min-height: 800px) {
  .game {
    border-radius: 40px;
    padding: 120px 20px 80px 20px;
    position: absolute;
    top: 48vh;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    width: 1000px;
  }

  .game__home {
    font-size: var(--medium);
    left: 5px;
    top: 5px;
  }

  .game__question {
    font-size: var(--xxlarge);
  }

  .game__answer-wrapper {
    margin-left: 45px;
  }

  .game__answer {
    font-size: var(--xxlarge);
    padding: 10px 20px;
    width: 260px;
  }

  .game__status {
    margin-top: 100px;
    width: 800px;
  }
}
.confetti-launcher {
  left: 50vw;
  position: absolute;
  top: 30vh;
}

@-webkit-keyframes great-work-animation {
  0% {
    background-color: rgba(255, 255, 255, 0);
    color: rgba(var(--main-color), 0);
  }
  7% {
    background-color: rgba(255, 255, 255, 0.6);
    color: rgb(var(--main-color));
  }
  30% {
    background-color: rgba(255, 255, 255, 0.6);
    color: rgb(var(--main-color));
  }
  100% {
    background-color: rgba(255, 255, 255, 0);
    color: rgba(var(--main-color), 0);
  }
}

@keyframes great-work-animation {
  0% {
    background-color: rgba(255, 255, 255, 0);
    color: rgba(var(--main-color), 0);
  }
  7% {
    background-color: rgba(255, 255, 255, 0.6);
    color: rgb(var(--main-color));
  }
  30% {
    background-color: rgba(255, 255, 255, 0.6);
    color: rgb(var(--main-color));
  }
  100% {
    background-color: rgba(255, 255, 255, 0);
    color: rgba(var(--main-color), 0);
  }
}
.confetti-launcher__message {
  -webkit-animation: 5s great-work-animation;
          animation: 5s great-work-animation;
  border-radius: 20px;
  font-size: var(--large);
  padding: 30px 30px;
  text-align: center;
  -webkit-transform: translate(-50%, -10vh);
          transform: translate(-50%, -10vh);
}

@media only screen and (min-width: 768px) {

  .confetti-launcher__message {
    border-radius: 40px;
    font-size: var(--xlarge);
  }

}

.report {
  align-items: center;
  border: 3px solid white;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  max-width: 1000px;
  padding-bottom: 15px;
  width: 100%;
}

.report__header {
  border-bottom: 3px solid hsl(0, 0%, 100%);
  color: rgb(var(--shadow-color));
  font-size: 40px;
  padding: 15px 0;
  position: relative;
  text-align: center;
  width: 100%;
}

.report__home {
  font-size: var(--small);
  left: 5px;
  position: absolute;
  top: 5px;
}

.report__leaderboard {
  border: 3px solid white;
  border-radius: 20px;
  margin: 25px 15px 0 15px;
}

.report__leaderboard-header {
  border-bottom: 3px solid white;
  font-size: var(--small);
  padding: 10px;
  text-align: center;
}

.report__leaderboard-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 15px;
}

.report__leaderboards--empty {
  display: none;
}

.report__sets {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 15px 15px 15px;
}

.report__set {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 15px;
}

.link-button--report:active {
  background-color: white !important;
}

@media only screen and (min-width: 768px) {

  .report {
    border-radius: 50px;
    padding-bottom: 25px;
  }

  .report__home {
    left: 20px;
    top: 10px;
  }
  
  .report__leaderboard {
    margin: 25px 25px 0 25px;
  }
  
  .report__leaderboard-body {
    padding: 20px;
  }

  .report__sets {
    margin: 0 25px 25px 25px;
  }
  
  .report__set {
    margin-top: 25px;
  }

}
.item-details {
  align-items: center;
  border: 3px solid white;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  height: 220px;
  justify-content: space-around;
  padding: 10px;
  position: fixed;
  width: 260px;
}

.item-details__header {
  font-size: var(--medium);
}

.item-details__close {
  border: 1px solid rgba(var(--main-color), 0.5);
  border-radius: 10px;
  box-shadow: 0px 0px 5px rgba(var(--shadow-color), 0.5);
  color: rgba(var(--main-color), 0.5);
  font-size: 18px;
  padding: 9px 13px;
  position: absolute;
  right: 8px;
  top: 8px;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}

.item-details__close:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media only screen and (min-width: 768px) {

  .item-details {
    border-radius: 50px;
    height: 300px;
    padding: 30px;
    width: 400px;
  }

  .item-details__header {
    font-size: 50px;
  }

  .item-details__close {
    right: 22px;
    top: 22px;
  }

}

.user-settings-range {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
 }

.user-settings-range__outer {
  display: flex;
  height: 50px;
  width: 100%;
}

.user-settings-range__inner {
  align-self: center;
  background: hsl(0, 0%, 100%);
  border: 1px solid rgba(var(--shadow-color), 0.5);
  border-radius: 4px;
  height: 5px;
  width: 100%;
}

.user-settings-range__thumb {
  align-items: center;
  background: #FFF;
  border: 3px solid rgba(var(--shadow-color), 0.8);
  border-radius: 20px;
  box-sizing: border-box;
  display: flex;
  font-size: 25px;
  height: 50px;
  justify-content: center;
  width: 50px;
}

.user-settings {
  align-items: center;
  display: flex;
  flex-direction: column;
  max-width: 500px;
  position: relative;
  width: 100%;
}

.user-settings__header {
  align-items: center;
  display: grid;
  font-size: 25px;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto;
  justify-items: center;
  margin-bottom: 15px;
  width: 100%;
}

.user-settings__header-title {
  grid-column: 1 / span 2;
  grid-row: 1;
  margin: 0 60px;
  text-align: center;
}

.user-settings__section {
  border: 3px solid hsl(0, 0%, 100%);
  border-radius: 40px;
  box-sizing: border-box;
  margin-bottom: 15px;
  width: 100%;
}

.user-settings__section-header {
  border-bottom: 3px solid hsl(0, 0%, 100%);
  color: rgb(var(--shadow-color));
  font-size: 40px;
  padding: 10px 0;
  text-align: center;
  width: 100%;
}

.user-settings__section-body {
  padding: 20px 40px;
}

.user-settings__question-range {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-size: 30px;
  margin: 15px auto 0 auto;
}

.user-settings__down-arrow {
  margin: 18px;
}

.user-settings__right-arrow {
  display: none;
}

.user-settings__home {
  font-size: var(--small);
  grid-column: 1;
  grid-row: 1;
}

@media only screen and (min-width: 768px) {

  .user-settings {
    max-width: 650px;
  }

  .user-settings__header {
    font-size: 50px;
    margin-bottom: 40px;
  }

  .user-settings__section {
    margin-bottom: 40px;
  }

  .user-settings__header-title {
    margin: 0 85px;
  }
  
  .user-settings__question-range {
    flex-direction: row;
    justify-content: space-around;
    width: 450px;
  }

  .user-settings__down-arrow {
    display: none;
  }

  .user-settings__right-arrow {
    display: block;
  }

  .user-settings__home {
    font-size: var(--medium);
  }

}

.mode-links {
  align-items: center;
  border: 3px solid hsl(0, 0%, 100%);
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  min-width: 280px;
}

.mode-links__header {
  border-bottom: 3px solid hsl(0, 0%, 100%);
  color: rgb(var(--shadow-color));
  font-size: 40px;
  padding: 10px 0;
  text-align: center;
  width: 100%;
}

.mode-links__subsets {
  align-items: center;
  border-bottom: 1px solid hsl(0, 0%, 100%);
  border-top: 1px solid hsl(0, 0%, 100%);
  display: flex;
  justify-content: space-evenly;
  padding: 10px 5px;
  width: 100%;
}

.mode-links__subset {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mode-links__subset--hidden {
  display: none;
}

.user {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.user__greeting {
  font-size: var(--small);
  max-width: 280px;
  text-align: center;
}

.user__mode-list {
  display: grid;
  grid-gap: 15px;
  gap: 15px;
  grid-template-columns: auto;
  grid-template-rows: repeat(4, auto);
  margin: 15px 0;
}

.easter-egg {
  font-size: 10px;
  margin: 10px;
}

@media only screen and (min-width: 768px) {

  .user__header {
    max-width: 600px;
  }

  .user__greeting {
    font-size: var(--large);
    max-width: 600px;
  }

  .user__mode-list {
    grid-gap: 40px;
    gap: 40px;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, auto);
    margin: 40px 0;
  }

  .easter-egg {
    font-size: 30px;
    margin: 10px;
  }

}
